.projects__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20%;
}

/* ================== Item ================== */
.projectItem__title {
    display: flex;
    justify-content: center;
    font-size: var(--h2-font-size);
}

.projectItem__content {
    margin: 2rem .5rem 0;
    text-align: center;
}

.projectItem__intro {
    font-size: var(--h4-font-size);
    color: var(--text-color);
}

.projectItem__more {
    display: flex;
    align-items: center;
    width: max-content;
    column-gap: .25rem;
    cursor: pointer;
    margin-top: 2rem;
}

.projectItem__more:hover {
    color: var(--title-color-effect)
}

.projectItem__icon {
    font-size: var(--h3-font-size);
}

.projectItem__data {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: var(--z-project);
    visibility: hidden;
}

.projectItem__data-active {
    visibility: visible;
}

.projectItem__box {
    position: relative;
    width: 40%;
    height: 50%;
}

.projects__close {
    position: absolute;
    cursor: pointer;
    top: .75rem;
    right: 1rem;
    font-size: 1.5rem;
}

.projectItem__detail {
    margin: 0 2rem;
    font-size: var(--h4-font-size);
    color: var(--title-color)
}

.projectItem__pair {
    display: flex;
    column-gap: 1rem;
    margin-bottom: .5rem;
}

.projectItem__key {
    font-weight: var(--font-semi-bold);
}

.projectItem__links {
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.projectItem__link {
    display: flex;
    column-gap: .25rem;
    align-items: center;
    width: max-content;
    font-size: var(--h4-font-size);
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
}

.projectItem__link:hover {
    color: var(--title-color-effect)
}